import {
  Color,
  ColorLevel,
  Layer,
  Space,
  Typo,
  colorLevels,
  colorsList,
  layersList,
  spacesList,
  typoList
} from '@hubert/styles';
import { ReactNode } from 'react';
import { PropsType } from './sb-improves';

export const RADIUS: PropsType<Space> = {
  description: 'Customize o border-radius desse component',
  type: 'select',
  options: spacesList,
  typeLabel: 'Space',
  typeDetail: `import { Space } from '@hubert/styles';`
};

export const WIDTH: PropsType<string> = {
  description: 'Customize o width desse component',
  type: 'text'
};

export const HEIGHT: PropsType<string> = {
  description: 'Customize o height desse component',
  type: 'text'
};

export const ID: PropsType<string> = {
  description: 'É necessario adicionar um id a este component',
  type: 'text'
};

export const COLOR: PropsType<Color> = {
  description: 'Customize a cor desse component',
  type: 'select',
  options: colorsList,
  typeLabel: 'Color',
  typeDetail: `import { Color } from '@hubert/styles';`
};

export const COLOR_LEVEL: PropsType<ColorLevel> = {
  description: 'Customize o nivel da cor desse component',
  type: 'select',
  options: colorLevels,
  typeLabel: 'ColorLevel',
  typeDetail: `import { ColorLevel } from '@hubert/styles';`
};

export const BG_COLOR: PropsType<Color> = {
  ...COLOR,
  description: 'Customize a cor de fundo desse component'
};

export const BG_COLOR_LEVEL: PropsType<ColorLevel> = {
  ...COLOR_LEVEL,
  description: 'Customize o nivel da cor de fundo desse component'
};

export const TYPO: PropsType<Typo> = {
  description: 'Customize o estilo de tipografia desse component',
  type: 'select',
  options: typoList,
  typeLabel: 'Typo',
  typeDetail: `import { Typo } from '@hubert/styles';`
};

export const PADDING: PropsType<Space> = {
  description: 'Customize o padding desse component',
  type: 'object',
  options: spacesList,
  typeLabel: 'Space|Space[]',
  typeDetail: `import { Space } from '@hubert/styles';`
};

export const LAYER: PropsType<Layer> = {
  description: 'Customize o zIndex/camada desse component',
  type: 'select',
  options: layersList,
  typeLabel: 'Layer',
  typeDetail: `import { Layer } from '@hubert/styles';`
};

export const FUNCTION: PropsType<ReactNode> = {
  description:
    'Executa ao disparar o evento citado (ex: "onClick" será disparado ao clicar)',
  type: 'none',
  typeLabel: 'Function'
};

export const ELEMENT: PropsType<ReactNode> = {
  description:
    'Componente inserido no contexto citado (ex: "left" será inserido no lado esquerdo)',
  type: 'none',
  typeLabel: 'ReactNode'
};

export const CHILDREN: PropsType<ReactNode> = {
  description: 'Componente filho, consulte os exemplos para mais informações',
  type: 'none',
  typeLabel: 'ReactNode'
};

export const LIST_OF = <T>(typeLabel: string): PropsType<T> => ({
  description: `Lista/Array de um tipo primitivo ou objeto, definição abaixo:`,
  type: 'none',
  typeLabel: typeLabel
});

export const VALUE: PropsType<never> = {
  description: 'O valor do estado atual do componente',
  type: 'none',
  typeLabel: 'T'
};

export const ON_CHANGE: PropsType<never> = {
  description: 'Função executada ao mudar o valor do estado',
  type: 'none',
  typeLabel: 'Function'
};
